 .text-container{
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 100vh; */
    width: 58vw;
    /* background: linear-gradient(135deg, #5098bd 0%, #46738a 100%); */
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

    box-sizing: border-box;
    margin: 0;
    padding: 0;

  }
  .container-fluid{
    background-color: #333;
  }
  .text-container {
    position: absolute;
    padding: 2rem;
   top: 11rem;
    border-radius: 10px;
    /* box-shadow: 0 8px 32px rgba(31, 38, 135, 0.15); */
    transition: transform 0.3s ease;
  
    display: flex;
    flex-direction: column;
    text-align: left;
    align-items: flex-start;
  }
  
  .text-container:hover {
    transform: translateY(-5px);
  }
  
  h1 {
    color: #ffffff;
    font-size: 3rem;
    font-weight: 300;
    line-height: 1.5;
    text-transform: uppercase;
    white-space: nowrap;
    position: relative;
    width: 650px;
    height: 4.5rem;
  }
  
  h1 span.prefix {
    font-size: 40px;
    margin-left: 40px;
    position: relative;
    cursor: pointer;
    transition: color 0.3s ease;
  }
  
  h1 span.prefix:hover {
    color: #9210b9;
  }
  
  .message {
    /* background: linear-gradient(45deg, #a8d8ea, #aa96da); */
    color: #ffffff;
    display: block;
    font-weight: 700;
    position: absolute;
    padding: 0.2rem 1rem;
    top: 0.2rem;
    left: 115px;
    border-radius: 4px;
    animation: openclose 8s ease-in-out infinite;
    height: 4.5rem;
    cursor: pointer;
    /* transition: transform 0.3s ease, box-shadow 0.3s ease; */
  }
  
  /* .message:hover {
    transform: scale(1.02);
    box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  } */
  
  .word1, .word2, .word3, .word4 {
    font-family: tahoma;
    position: absolute;
    width: 100%;
    opacity: 0;
    height: 4.5rem;
  }
  
  .message .word1 { animation: word-animation 8s infinite; }
  .message .word2 { animation: word-animation 8s infinite 2s; }
  .message .word3 { animation: word-animation 8s infinite 4s; }
  .message .word4 { animation: word-animation 8s infinite 6s; }
  
  @keyframes word-animation {
    0%, 5% { opacity: 0; }
    10%, 22% { opacity: 1; }
    27%, 100% { opacity: 0; }
  }
  
  @keyframes openclose {
    0% { top: 0.2rem; width: 0; }
    5% { width: 0; }
    10% { width: 285px; }
    20% { top: 0.2rem; width: 285px; }
    25% { top: 0.2rem; width: 0; }
    30% { top: 0.2rem; width: 0; }
    35% { top: 0.2rem; width: 285px; }
    45% { top: 0.2rem; width: 285px; }
    50% { top: 0.2rem; width: 0; }
    55% { top: 0.2rem; width: 0; }
    60% { top: 0.2rem; width: 285px; }
    70% { top: 0.2rem; width: 285px; }
    75% { top: 0.2rem; width: 0; }
    80% { top: 0.2rem; width: 285px; }
    90% { top: 0.2rem; width: 285px; }
    95% { top: 0.2rem; width: 0; }
    100% { top: 0.2rem; width: 0; }
  }
  
  @media (max-width: 768px) {
    h1 {
      font-size: 2rem;
      width: 400px;
    }
    
    h1 span.prefix {
      font-size: 1.4rem;
      margin-left: 20px;
    }
    
    .message {
      left: 150px;
    }
  }




  .image-yasindu{
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }

  .image-contain{
    width: 20rem;
    height: 20rem;
    position: absolute;
    right: 11rem;
    top: 14rem;
  }

 .my-name{
    margin-left: 2rem;
  }

 .description-title{
