}







@import url('https://fonts.googleapis.com/css?family=Lato:100&display=swap');



.download {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 25%;
  margin-left: 7rem;
  margin-top: 2rem;

  display: flex;
  margin-left: 19rem;
}


.btn {
  width: 180px;
  height: 60px;
  cursor: pointer;
  background: transparent;
  border: 1px solid #91C9FF;
  outline: none;
  transition: 1s ease-in-out;
}

svg {
  position: absolute;
  left: 0;
  top: 0;
  fill: none;
  stroke: #fff;
  stroke-dasharray: 150 480;
  stroke-dashoffset: 150;
  transition: 1s ease-in-out;
}

.btn:hover {
  transition: 1s ease-in-out;
  background: #4F95DA;
}

.btn:hover svg {
  stroke-dashoffset: -480;
}

.btn span {
  color: white;
  font-size: 18px;
  font-weight: 100;
}